___CSS_LOADER_EXPORT___.push([module.id, `.plaques-section {
    background-color: #f0f0f0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 40px 20px;
    margin: 0 auto;
  }
  
  .plaque-card {
    background-color: #fff;
    border-radius: 8px;
    width: 90%;
    max-width: 400px;
    text-align: center;
    padding: 30px 20px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08), 
    0 4px 6px rgba(0, 0, 0, 0.04);

    transition: all 0.5s ease-in-out;
    transition-delay: 0.5s;
  }
  
  .plaque-icon {
    width: 67px;
    height: 67px;
    margin-bottom: 15px;
    object-fit: contain;
  }
  
  .plaque-title {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
      Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1.3rem;
    font-weight: 600;
    color: #333;
    margin: 0;
  }
  
  .plaque-description {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: max-height 0.4s ease-out, opacity 0.4s ease-out;
    transition-delay: 0.5s;
    font-size: 0.9rem;
    color: #555;
    line-height: 1.5;
    padding: 0 10px;
  }
  
  .plaque-description.visible {
    max-height: 200px;
    opacity: 1;
    padding-top: 15px;
  }
